.alignnone {
  margin: $spacer 0; }

.aligncenter {
  display: block;
  clear: both;
  margin: $spacer auto; }

.alignright {
  float: right;
  clear: right;
  margin: $spacer / 2 0 $spacer $spacer / 2; }

.alignleft {
  float: left;
  clear: left;
  margin: $spacer / 2 $spacer / 2 $spacer 0; }

img {
  &.alignnone {
    vertical-align: middle; } }

.wp-caption {
  img {
    display: block;
    max-width: 100%; }

  p {
    &.wp-caption-text {
      margin: 0;
      padding: $spacer / 4 $spacer / 4 $spacer / 2;
      font-size: 0.8em; } } }

.screen-reader-text {
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  @include position-absolute;

  &:active,
  &:focus {
    width: auto;
    height: auto;
    overflow: visible;
    clip: auto;
    white-space: normal;
    @include position-static; } }
