.thumbnail {
  display: block;

  img {
    display: block;
    margin: auto;
    max-width: 100%;
    height: auto; }

  &_link {
    z-index: 0;
    cursor: pointer;
    @include position-relative;

    &::after {
      transition: opacity 0.3s ease;
      opacity: 0;
      z-index: 1;
      background: linear-gradient(rgba($black, 0), rgba($black, 1));
      content: '';
      @include position-absolute(0, 0, 0, 0); } } }
