.admin-bar {
  #wpadminbar {
    transition: width 0s 1s, opacity 0.3s;
    opacity: 0.7;
    width: 32px;
    min-width: 0;
    height: 32px;
    overflow: hidden;

    &:hover {
      transition: width 0s 0s, opacity 0.3s;
      opacity: 1;
      width: 100%;
      overflow: visible; }

    @include breakpoint-max(782) {
      transition: none;
      opacity: 1;
      width: 100%;
      height: 46px;
      overflow: visible; } } }
